import react, { useState, useEffect } from "react";
import { Link } from "react-router-dom";

const InstaBar = () => {
  const navItems = [
    {
      id: "1",
      name: "Home",
      icon: "fa fa-home",
      link: "/"
    },
    {
      id: "2",
      name: "messenger",
      icon: "fa-brands fa-facebook-messenger",
      link: "/chat"
    },
    {
      id: "3",
      name: "Add",
      icon: "fa-solid fa-square-plus",
      link: "/add"
    },
    {
      id: "4",
      name: "Safari",
      icon: "fa-brands fa-safari",
      link: "/safari"
    },
    {
      id: "5",
      name: "Likes",
      icon: "fa-solid fa-heart",
      link: "/notifications"
    },
    {
      id: "6",
      name: "Profile",
      icon: "fa fa-user",
      link: "/profile"
    }
  ];

  return (
    <>
      <div className="insta-bar">
        <div className="left-side-nav">Instagram</div>
        <div className="center-nav">
          <input
            type="search"
            placeholder="Search"
            className="search-bar"
            name="search"
          />
        </div>
        <div className="right-side-nav">
          {navItems.map((item) => (
            <Link to={item.link}>
              <i className={item.icon + " nav-icon"} aria-hidden="true"></i>
            </Link>
          ))}
        </div>
      </div>
    </>
  );
};

export default InstaBar;
