import Message from "../HomeComponents/Message";

const Messages = () => {
  const Messages = [
    {
      name: "iron.man",
      message: "Hello",
      status: "seen just now"
    },
    {
      name: "scarlett_johnson",
      message: "See You Later",
      status: "seen 1hr ago"
    },
    {
      name: "tara_909",
      message: "How r u",
      status: "sent 1 week ago"
    },
    {
      name: "mayank",
      message: "That's Good",
      status: "seen 21 week ago"
    }
  ];
  return (
    <>
      <div className="post-flex">
        <div className="col-md-3" style={{ marginTop: "5px" }}></div>
        <div className="col-md-4">
          {Messages.map((item, index) => (
            <Message message={item} key={item.id} index={index} />
          ))}
        </div>
        <div className="col-md-5" style={{ marginTop: "5px" }}></div>
      </div>
    </>
  );
};

export default Messages;
